* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  z-index: 1;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

@media only screen and (width <= 767px) {
  .header {
    padding: 22px 0;
  }
}

.header.scrolled {
  background: #252b49;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
}

.header__container:after {
  content: "";
  background: #babbc5;
  width: calc(100% - 30px);
  height: 1px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media only screen and (width <= 767px) {
  .header__container:after {
    display: none;
  }
}

@media only screen and (width <= 1024px) {
  .header__logo-link {
    max-width: 120px;
  }
}

.header__logo {
  width: 100%;
  display: flex;
}

.header__menu {
  column-gap: 10px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

@media only screen and (width <= 1024px) {
  .header__menu {
    column-gap: 9px;
  }
}

.header__link {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  border-bottom: 3px solid #0000;
  min-width: 129px;
  padding: 36px 0;
  font-family: Raleway, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 15px;
  text-decoration: none;
  transition: all .3s linear;
  display: block;
}

@media only screen and (width <= 1024px) {
  .header__link {
    min-width: 100px;
    padding: 41px 17px;
  }
}

@media only screen and (width <= 767px) {
  .header__link {
    min-width: max-content;
    padding: 41px 17px;
  }
}

.header__link:hover {
  color: #fff;
  border-bottom: 3px solid #babbc5;
}

.header__link:active {
  color: #e71414;
  border-bottom: 3px solid #e71414;
}

@media only screen and (width <= 767px) {
  .header__nav {
    display: none;
  }

  .header__nav.open {
    width: 100%;
    height: 100vh;
    padding: 84px 20px 20px;
    display: block;
    position: fixed;
    top: 0;
    left: 0;
  }

  .header__nav .header__menu {
    display: block;
  }

  .header__nav .header__link {
    text-align: left;
    padding: 24px 0;
    display: block;
  }
}

.header__burger {
  width: 24px;
  height: 18px;
  display: none;
  position: relative;
}

@media only screen and (width <= 767px) {
  .header__burger {
    display: block;
  }
}

.header__burger:before {
  content: "";
  border: 3px solid #fff;
  width: 100%;
  transition: all .3s linear;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.header__burger:after {
  content: "";
  border: 3px solid #fff;
  width: 100%;
  transition: all .3s linear;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header__burger.open .header__burger-item {
  opacity: 0;
  transition: all .3s linear;
}

.header__burger.open:after {
  transform: rotate(-45deg)translate(5px, -5px);
}

.header__burger.open:before {
  transform: rotate(45deg)translate(5.8px, 5.8px);
}

.header__burger-item {
  border: 3px solid #fff;
  width: 120%;
  transition: all .3s linear;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

